const $ul = $('main ul')
const $lastLi = $ul.find('li.last')
const x = localStorage.getItem('x')
const xObject = JSON.parse(x)

const hashMap = xObject || [
    { logo: 'B', url: 'https://baidu.com' },
    { logo: 'C', url: 'https://codepen.io' },
    { logo: 'G', url: 'https://google.com'},
    { logo: 'W', url: 'https://weibo.com'}
]

const simplifyUrl = (url) => {
    return url.replace('https://', '')
        .replace('http://', '')
        .replace('www.', '')
        .replace(/\/.*/, '')
}

const render = ()=> {
    $ul.find('li:not(.last').remove();
    hashMap.forEach((node, index) => {
        const $li = $(`<li>
            <div class="site">
                <div class="logo">${node.logo}</div>
                <div class="link">${simplifyUrl(node.url)}</div>
                <div class="close">
                    <svg class="icon" >
                        <use xlink:href="#icon-close"></use>
                    </svg>
                </div>
            </div>
        </li>`).insertBefore($lastLi)
        $li.on('click', () => {
            window.open(node.url)
        })
        $li.on('click', '.close', (e) => {
            e.stopPropagation()
            hashMap.splice(index, 1)
            render()
        })
    })
}

render();

$('.addSite')
    .on('click', () => {
        let url = window.prompt('请输入添加的网址?');
        if (url.indexOf('http') !== 0){
            url = 'https://' + url
        }
        if (simplifyUrl(url) !== ''){
            hashMap.push({
                logo: simplifyUrl(url)[0],
                url: url
            })
        }
        render ();
    })

window.onbeforeunload = () => {
    const string = JSON.stringify(hashMap)
    localStorage.setItem('x', string)
}

$(document).on('keypress', (e)=>{
    const key = e.key // const{key} = e
    for(let i=0;i<hashMap.length;i++){
        if(hashMap[i].logo.toLowerCase() === key){
            window.open(hashMap[i].url)
        }
    }
})